import React from "react";
import Typical from "react-typical";

export default function Section(props){
    const {them} = props
    const handleDownload = () => {
        const link = document.createElement('a');
        link.href = `${process.env.PUBLIC_URL}/CV_KHALID_SBITI.pdf`;
        link.download = 'CV_KHALID_SBITI.pdf';
        link.click();
        
    };
    return(
        <div className="py-3">
            <div className='imageProfile d-flex aligne-items-center justify-content-center'>
                <div></div>
            </div>
            <br />
            <h2 className='txt w-100 text text-center'>KHALID SBITI</h2>

        <Typical
          steps={[
            "Web Developer", 3000,  // Pause time between text changes (in ms)
            "Back-End Developer", 3000,
            "Front-End Developer", 3000
          ]}
          typeSpeed={100}   // Slow typing speed (in ms per character)
          backSpeed={100}    // Slower backspacing speed (in ms per character)
          backDelay={3000}  // Delay before backspacing starts (in ms)
          loop={Infinity}   // Loop forever
          wrapper="p"    // Wrap the text in a span
          className="text txt text-center"
        />
            <div className="d-flex aligne-items-center justify-content-center">
                <a className="mx-1"  target="_blank" href="https://www.linkedin.com/in/sbiti-khalid/">
                {them === "clear"?
                <img src="data:image/png;base64,"/>
                    :
                    <img src="data:image/png;base64,"/>
                    }
                </a>
                <a target="_blank" className="mx-1"  href="https://github.com/SBITI-KHALID">
                {them === "clear"?
                    <img src="data:image/png;base64,"/>
                    :
                    <img src="data:image/png;base64,"/>
                    }
                </a>
                </div>
                <table className=" infor w-100 txt text text-center my-4">
        <tbody id="infoTableBody">
            <tr>
                <td className="icon">
                    {them === "clear"?
                    <img  src="data:image/png;base64,"/>
                    :    
                    <img  src="data:image/png;base64,"/>                    
                    }
                </td>
                <td className="px-2">0625092086</td>
            </tr>
            <tr>
                <td className="icon">
                    {them === "clear"?
                    <img  src="data:image/png;base64,"/>                    :    
                    <img  src="data:image/png;base64,"/>                    
                    }
                </td>
                <td className="px-2">sbitikhalid0@gmail.com</td>
            </tr>
            <tr>
                <td className="icon">
                {them === "clear"?
<img src="data:image/png;base64,"/>                    
:    
<img src="data:image/png;base64,"/>                    }
                </td>
                <td className="px-2">SIDI ABDALAH GHIAT</td>
            </tr>
            <tr>
                <td className="icon">
                    {them === "clear"?
                    <img src="data:image/png;base64,"/>
                :   
                <img src="data:image/png;base64,"/> 
                }
                </td>
                <td className="px-2">29/10/2003</td>
            </tr>
        </tbody>
    </table>
            <div className="w-100 text text-center">
                <button className="dwnCV"  onClick={handleDownload} >
                Download CV
                </button>
            </div>
        </div>

    )
}